import React, { Component } from 'react';
import styled, { keyframes, injectGlobal } from 'styled-components';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';
import MotionMenu from 'react-motion-menu'

class App extends Component {
  state = {
  }
  clickHide = () => {
    this.setState({
      animate: 'animated fadeOutLeft'
    })
  }

  render() {
    const profileUrl = 'https://scontent-arn2-1.xx.fbcdn.net/v/t1.0-9/13406711_1101861166521687_5147495652535716651_n.jpg?oh=08f4ede3d6b15fb049c3e3f5a84b7e79&oe=59652FC6';
    return (
      <Router>
        <Container>
          <Route exact path="/" render={() => (
            <Main className={this.state.animate}>
              <HeaderLine fadeIn><Box className="small">H</Box>ELLO.</HeaderLine>
              <UnderLine fadeIn="Right">WELCOME TO VL <SpanText color="coral">.DESIGNS</SpanText></UnderLine>
            </Main>
            )}/>
          <Route exact path="/about" render={() => (
            <Main className="aboutHight">
              <img className="animated flipInY w3-circle" src={profileUrl} width="270" height="270" style={{marginTop: '-50px'}} alt="Profile"/>
              <AboutTitle id="aboutTitle"><Sdelay delay="1" className="animated fadeIn">Hi.</Sdelay> <Sdelay delay="1.7" className="animated fadeIn">My name is </Sdelay><Sdelay delay="2.1" className="animated fadeIn"><SpanText color="coral">Viktor</SpanText></Sdelay><Sdelay delay="3.1" className="animated fadeIn"> and i do</Sdelay> <Sdelay delay="4.1" className="animated flipInX">fullstack web development.</Sdelay></AboutTitle>
            </Main>
            )}/>
          <Route path="/art" render={() => (
            <Main>
              <HeaderLine fadeIn><Box add color="LightSlateGray">A</Box>rt.</HeaderLine>
            </Main>
            )}/>
            <Route path="/code" render={() => (
            <Main>
              <HeaderLine fadeIn><Box add color="orangered">C</Box>ode.</HeaderLine>
            </Main>
            )}/>
          <NavSection>
            <Navigation navitems={['home', 'about', 'art', 'code']} hide={this.clickHide}/>
            <MobileNavigation/>
          </NavSection>
        </Container>
      </Router>
    );
  }
}
const Sdelay = styled.span`
  animation-delay: ${props => props.delay ? props.delay : '0'}s;
`

//<HeaderLine fadeIn><Box add color="RoyalBlue">A</Box>bout<Hover color="white">.</Hover></HeaderLine>

          // <NavSection>
          //   <Navigation navitems={['home', 'about', 'art', 'login']} hide={this.clickHide}/>
          // </NavSection>
const HeaderLine = ({ children, fadeIn }) => <span id="headerline"><Header id="title" className={`animated ${fadeIn ? 'fadeIn' : ''}`}><Title>{children}</Title></Header></span>
const UnderLine = ({ children, fadeIn }) => <span id="underline"><SmallTile id="undertitle" className={`animated fadeIn${fadeIn ? fadeIn : ''}`}>{children}</SmallTile></span>


const Navigation = ({navitems, hide}) => {
  return (
      <ul id="wide-nav">
        {navitems.map((item, i) => <List className="animated fadeInLeft" delay={i/2} key={i}><Link to={`/${item === 'home' ? '' : item}`} className="hvr-underline-from-center" href="#">{item.toUpperCase()}</Link></List>)}
      </ul>
    );
}




const MobileNavigation = () => (
        <MobileNav id="mobilenavigation">
          <MotionMenu
            type="horizontal"
            wing
            margin={60}
          >
            <MenuBox></MenuBox>
            <Rec to="/about" className="mobilenav">About
            </Rec>
            <Rec to="/art" className="mobilenav">Magic
            </Rec>
            <Rec to="/" className="mobilenav">Home
            </Rec>
            <Rec to="/code" className="mobilenav">Code
            </Rec>
          </MotionMenu>
        </MobileNav>
  )

const MobileNav = styled.div`
  display: none;
`;
const FadeOut = keyframes`
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    display: none;
  }
`
const MenuBox = styled.a`
  background-color: rgba(139,139,139, 0.7);
  width: 50px;
  height: 30px
  font-size: 20px;
  border-radius: 25px;
  &:hover {
    background-color: rgba(139,139,139, 1);
  }
`
// eslint-disable-next-line
injectGlobal`
  @media only screen and (max-width: 670px) {
    #wide-nav {
      animation: ${FadeOut} 0.1s;
      animation-fill-mode: forwards;
    }

    #headerline {
      h1 {
        font-size: 74px;
        margin-top: 300px
        height: 20px!important
        .small {
          max-height: 90px!important;
        }
        div {
          max-height: 93px!important;
          padding-left: 30px;
        }
      }
      h4 {
        font-size: 60px
      }
    }
    #underline {
      h4 {
        padding-top: 10px;
        font-size: 20px;
        letter-spacing: 1.5px;
        margin-bottom: 400px;
      }      
    }
    .w3-circle {
      height: 230px
      width: 230px
      margin-bottom: -55px
    }
    #aboutTitle {
      font-size: 19px
      width: 440px
      padding-top: 60px
    }
  }
  @media only screen and (max-width: 670px) {
    #mobilenavigation {
      display: flex;
      justify-content: center;
      align-items: center
      margin-right: 50px;

    }
  }
`;

const Rec = styled(Link)`
  padding-right: 20px;
  color: rgba(139,139,139, 0.7);
  text-align: center;
  > a {

    font-size: 10px!important;
  }
  &:hover {
    color: rgba(139,139,139, 1)
  }

`

const Container = styled.div`
  max-width: 100%;
  height: 100%;
  margin: auto;
  display: flex;
  justify-content: center
  align-items: center
  flex-direction: column
`;
const Main = styled.main`
  margin-bottom: 270px

`;

const NavSection = styled.section`
  position: relative
  bottom: 100
  z-index: 1;
`;

const List = styled.li`
  list-style: none
  display: inline
  color: rgba(139,139,139, 0.8)
  padding: 50px
  animation-delay: ${props => props.delay ? 2 + props.delay + 's' : '2s'}
`;


const SpanText = styled.span`
  color: ${props => props.color ? props.color : 'white'}
`;

const colorchange = keyframes`
  from {
    background-color: #d34b3b
  }
  to {
    background-color: tomato
  }
`;

const Box = styled.div`
  max-height: 165px
  max-width: 160px
  padding-left: 50px
  padding-right: ${props => props.add ? '10px' : '1px'}
  display: inline-block
  background-color: ${props => props.color ? props.color : '#d34b3b'}
  color: white;
  ${props => props.color ? '' : `animation: ${colorchange} 2s linear infinite alternate`}
`;

const Header = styled.div`
  animation-delay: 0.5s
  animation-duration: 3s
`;
const Title = styled.h1`
  font-weight: 700
  font-size: 134px
  color: ${props => props.color ? props.color : '#4f4f4f'}
`;

const SmallTile = styled.h4`
  font-weight: 600
  font-size: 30px
  color: ${props => props.color ? props.color : '#8b8b8b'}
  position: absolute
  animation-delay: 1s
  animation-duration: 1.5s
  text-align: center
  padding: 0 0px 0 0px
  margin-top: -30px
  letter-spacing: 5px
`;

const AboutTitle = styled(SmallTile)`
  margin: auto;
  padding-right: 0;
  letter-spacing: 0px;
  width: 500px;
  margin-left: -100px
  padding-top: 20px;
  font-size: 24px
`;
// const colors = {
//   background: '#f6f4f4',
//   title: '#4f4f4f',
//   smalltitle: '#8b8b8b',
//   box: '#d34b3b'
// }















export default App;



